import { Box, Typography } from "@mui/material";
import React, { useEffect, useState } from "react";
import { motion } from "framer-motion";

const boxAnimation = {
    hidden: {
        x: -100,
        opacity: 0,
    },
    visible: {
        x: 0,
        opacity: 1,
        transitionDuration: "1s",
    },
};
const boxAnimationL = {
    hidden: {
        x: 100,
        opacity: 0,
    },
    visible: {
        x: 0,
        opacity: 1,
        transitionDuration: "1s",
    },
};

function StartedBox({ title, logo, l }) {
    return (
        <motion.div
            initial="hidden"
            whileInView="visible"
            variants={l ? boxAnimation : boxAnimationL}
        >
            <Box
                sx={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: `${l ? "normal" : "flex-end"}`,
                    width: { xs: "400px", lg: "550px" },
                    backgroundImage:
                        "url(https://crew-c.com/community/images/bg-first-item-4.png)",
                    borderRadius: "5px",
                }}
            >
                {l ? (
                    <>
                        <Typography
                            variant="h6"
                            sx={{
                                width: "300px",
                                color: "white",
                                textAlign: "right",
                                fontWeight: "650",
                            }}
                        >
                            {title}
                        </Typography>
                        <img src={`${logo}`} alt="" />
                    </>
                ) : (
                    <>
                        <img src={`${logo}`} alt="" />
                        <Typography
                            variant="h6"
                            sx={{
                                width: "300px",
                                color: "white",
                                textAlign: "left",
                                fontWeight: "650",
                            }}
                        >
                            {title}
                        </Typography>
                    </>
                )}
            </Box>
        </motion.div>
    );
}

export default StartedBox;
