import { Box, Typography } from "@mui/material";
import React, { useState } from "react";
import { motion } from "framer-motion";
const boxAnimation = {
    hidden: {
        x: -200,
        opacity: 0,
    },
    visible: {
        x: 0,
        opacity: 1,
        transitionDuration: "1s",
    },
};
const boxAnimation2 = {
    hidden: {
        x: 200,
        opacity: 0,
    },
    visible: {
        x: 0,
        opacity: 1,
        transitionDuration: "1s",
    },
};
function AboutItem({ body, title, logo, l }) {
    const [focus, setFocus] = useState(false);
    return (
        <motion.div
            initial="hidden"
            whileInView="visible"
            variants={l ? boxAnimation : boxAnimation2}
        >
            <Box
                sx={{
                    border: "double 1px transparent",
                    borderRadius: "5px",
                    transitionDuration: "500ms",
                    backgroundImage: `linear-gradient(${
                        focus ? "green,5%, #0B0C0E" : "#131416,#0B0C0E"
                    }), radial-gradient(circle at top left, green,black)`,
                    backgroundOrigin: "border-box",
                    backgroundClip: "content-box, border-box",
                    marginLeft: "30px",
                }}
                onMouseOver={() => setFocus(true)}
                onMouseOut={() => setFocus(false)}
            >
                <Box
                    sx={{
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        position: "relative",

                        gap: "60px",
                        width: { xs: "500px", md: "770px" },
                        margin: "40px",
                    }}
                >
                    <Box
                        sx={{
                            position: "absolute",
                            left: "29px",
                            top: "8px",
                            border: "15px solid #58BF45",
                            borderRadius: "50%",
                            transitionDuration: "500ms",
                            opacity: `${focus ? "1" : "0"}`,
                        }}
                    />
                    <Box
                        sx={{
                            display: "flex",
                            justifyContent: "center",
                            alignItems: "centr",
                            backgroundSize: "cover",
                            backgroundImage:
                                "url(https://crew-c.com/community/images/bg-about-block-image.png)",
                            zIndex: "1",
                        }}
                    >
                        <img
                            style={{
                                position: "relative",
                                left: "5px",
                                bottom: "5px",
                                width: "130px",
                            }}
                            src={`${logo}`}
                            alt=""
                        />
                    </Box>
                    <Box>
                        <Typography
                            variant="h5"
                            sx={{
                                fontWeight: "600",
                                mb: "1px",
                                color: "white",
                            }}
                        >
                            {title}
                        </Typography>

                        <Typography
                            sx={{
                                color: "rgba(255,255,255,0.6)",
                                fontSize: "17px",
                                width: { xs: "400px", md: "500px" },
                            }}
                        >
                            {body}
                        </Typography>
                    </Box>
                </Box>
            </Box>
        </motion.div>
    );
}

export default AboutItem;
